@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: Open Sans;
	font-weight: 700;
	font-size: 2rem;
	background-color: #3c1536; /* #28afb0; */
	color: #ffffff;
}

.App {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	width: 35rem;
	text-align: center;
}

.App * {
	padding-top: 5px;
}

.break,
.session {
	display: inline-block;
	text-align: center;
	width: 50%;
	height: 50%;
}

.rest {
	padding-top: 20px;
	margin: 0 auto;
	width: 100%;
	text-align: center;
}

.rest * {
	padding-top: 5px;
}

button,
button:link,
button:visited {
	padding-top: 0.5rem;
	display: inline-block;
	transition: all 0.2s;
	position: relative;
	border: none;
	outline: none;
	cursor: pointer;
	width: 3rem;
	background: none;
	color: white;
	font-size: 2rem;
}

button > * {
	pointer-events: none;
}

